<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
  html,body{
    width: 100%;
    height: 100%;
  }
  html,body,div,p,h3,ul,li{
    margin: 0;
    padding: 0;
  }
  #app {
    height: 100%;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #333;
    background: #f4f4f4;
  }
  .fileList{
    padding: 0 2%;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
  }
  .page-title{
    height: 88px;
    line-height: 88px;
  }
  .list-con{
    height: calc(100% - 88px);
    background: #fff;
    border-radius: 8px;
    padding: 64px 5%;
    box-sizing: border-box;
  }
  .search-box{
    font-size: 24px;
  }
  .search-box .el-input__inner{
    height: 60px;
    line-height: 60px;
    border-radius: 8px;
    font-size: 18px;
  }
  .total-text{
    font-size: 16px;
    margin-top: 60px;
  }
  .list{
    margin-top: 16px;
    border-top: 1px solid #ddd;
  }
  li{
    list-style: none;
    height: 46px;
    line-height: 46px;
    border-bottom: 1px solid #e1e1e1;
    padding: 0 2%;
    cursor: pointer;
  }
  li:hover{
    background: #f9f9f9;
  }
  .el-icon-arrow-left{
    cursor: pointer;
  }
</style>



