import Vue from 'vue'
import Router from 'vue-router'
import fileList from '@/components/fileList'
import fileView from '@/components/fileView'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'fileList',
      component: fileList
    },{
      path: '/view',
      name: 'fileView',
      component: fileView
    }
  ],
  mode: 'history'
})



