<template>
  <div class="fileList">
    <h3 class="page-title">
      <i class="el-icon-arrow-left" @click="goBack"></i>
      电泳双轨自行输送小车作业安全操作规程</h3>
    <div class="list-con">
      <h4>一、适用范围</h4>
      <p>本规程适用电泳双轨自行输送小车作业人员的安全操作。操作人员应遵守《安全生产总则》、《设备操作规程》。</p>
      <h4>二、岗位安全作业职责</h4>
      <p>
        1.负责本岗位日常事故隐患自我排查治理，包括班前、班中、班后的排查和处置。
        2.负责本岗位设备操作或其他有关作业，在作业和故障排除过程中，严格按照规定安全操作，正确佩戴和使用劳动防护用品。
        3.负责本岗位设备设施及其安全装置的日常保养，保养过程按规定安全作业，确保本岗位使用的设备设施及其安全装置完好有效，本岗位设备、设施故障应及时报修。
        4.负责本岗位事故和紧急情况的报告和现场处置。
      </p>
      <h4>三、岗位主要危险有害因素（危险源）</h4>
      作业活动	主要危险有害因素	可能造成的事故/伤害	可能伤害的对象
      电泳双轨自行输送小车作业	电气操作	设备漏电、缆线损坏	触电	操作人员和
      周边人员
        设备操作	起重伤人、运行中挤撞人员、联锁失效、急停失效	起重伤害、机械伤害	操作人员和
      周边人员
        作业人员	警戒区域内作业	机械伤害	操作人员和
      周边人员
      <h4>四、劳动防护用品穿戴要求</h4>
      <p>针对作业操作人员应规范穿戴劳保工作服、安全帽、防护口罩、防砸工作鞋。</p>
      <h4>五、电泳双轨自行输送小车安全操作要求</h4>
      <p>
         ⑴工作前
        1.操作者必须拥有该设备的操作证，操作者应熟悉本机的结构和性能，了解设备工作原理，严格遵守安全操作规程。
        2.遵守有关起重机设备的安全操作规程，必须熟记并遵守起重设备“十不吊”的要求。
        3.操作者必须正确穿戴劳动防护用品。非操作人员严禁使用设备。
        4.保持现场、设备平台及周围整洁无垃圾杂物。 
        ⑵工作中
        1.小车运行过程中,操作人员必须注意并提醒周边人员禁止在轨道下方停留或穿行，防止高空坠物伤害。
        2.操作人员发现问题应及时断电并通知有关维修人员,不能擅自离开岗位。不得远离急停按钮的操作范围。
        ⑶工作后
        1.做好日常保养工作，使设备现场及周边环境达到整齐、清洁、润滑、安全。
      </p>
      <h4>六、岗位应急处置</h4>     
      <p>
        1.发生人身伤害时，应立即停止工作将受伤人员进行转移出作业室场地。对受伤人员进行包扎、止血等处理，防止感染；严重的应立即到医院处置。
      </p>

    </div>
  </div>
</template>

<script>
export default {
  name: 'fileView',
  data() {
    return {

    }
  },
  methods: {
    goBack() {
      this.$router.go(-1)
    }
  }
}
</script>


