<template>
  <div class="fileList">
    <h3 class="page-title">文件列表</h3>
    <div class="list-con">
      <el-input
        placeholder="请输入文档名称"
        suffix-icon="el-icon-search"
        v-model="searchText"
        class="search-box"
      />
      <p class="total-text">总 <strong>1</strong></p>
      <ul class="list">
        <li v-for="(file, idx) in fileList" :key="idx" @click="viewDoc">
          <i class="el-icon-document"></i>
          {{ file.name }}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'fileList',
  data () {
    return {
      searchText: '',
      fileList: [{
        name: '电泳双轨自行输送小车作业安全操作规程.docx'
      }]
    }
  },
  methods: {
    viewDoc() {
      this.$router.push('/view')
    }
  }
}
</script>


